<template>
  <div>
    <b-nav tabs>
      <b-nav-item>
        <router-link to="/food">食料品</router-link>
      </b-nav-item>
      <b-nav-item active>
        <router-link to="/drink">飲料水</router-link>
      </b-nav-item>
      <b-nav-item>
        <router-link to="/medicine">医薬品</router-link>
      </b-nav-item>
    </b-nav>
    <Chart :chartData="chartData" :chartOptions="chartOptions"></Chart>
  </div>
</template>
<script>
import Chart from './Chart.vue'
var labels = [];
var date = new Date();
for(var i=0; i<7; i++){
  var _d = new Date(date.setDate(date.getDate()-1)).toLocaleDateString("ja-JP", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
  })
  .split("/")
  .join("-");
  labels.push(_d)
}
export default{
  components: {
    Chart
  },
  data: function(){
    return{
      chartData: {
        labels: labels,
        datasets: [
          {
            label: 'Data One',
            backgroundColor: '#00ff00',
            borderColor: '#00ff00',
            data: [100, 50, 80, 40, 39, 70, 40]
          }
        ]
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false
      }
    }
  }
}

</script>